import axios from 'axios';
// const axios = require('axios');

export const createPost = async (title, content) => {
  try {
    const res = await axios({
      method: 'POST',
      url: '/posts/create-post',
      data: {
        title,
        content,
      },
    });

    if (res.data.status === 'success') {
      console.log('Post created!');
      window.setTimeout(() => {
        location.assign('/');
      }, 1500);
    }
  } catch (err) {
    console.log(err);
  }
};
